.movie {
  background-color: #fafafa;
  margin-bottom: 70px;
  padding: 20px;
  display: grid;
  grid-template-columns: min-content(150px, 1fr);
  grid-template-rows: repeat(3, minmax(26.67px, auto));
  grid-gap: 20px;
  text-decoration: none;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3);
}

.movie__top {
  display: flex;
}

.movie__img {
  width: 100%;
  max-width: 150px;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.5);
}

.movie__link {
  text-decoration: none;
}

.movie__title,
.movie__year {
  color: #222;
}

.movie__rate {
  text-align: center;
  padding: 10px;
  font-size: 15px;
  color: #fafafa;
  background-color: #0d47a1;
  border-radius: 12px;
  position: relative;
}

.movie__summary {
  font-size: 18px;
  margin-bottom: 10px;
  height: 150px;
}

.movie__genres {
  position: relative;
  height: 40px;
  margin: 0;
  padding-left: 0px;
  display: flex;
}

.genres__item {
  list-style: none;
  margin-right: 12px;
  padding: 4px;
  border: 2px solid #0d47a1;
  border-radius: 12px;
  line-height: 25px;
}
