.container {
  height: 100%;
  display: flex;
  justify-content: center;
  background-color: #222;
  color: #fafafa;
  padding-bottom: 40px;
}

.loading {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fafafa;
  font-weight: 300;
}

.titleAndYear {
  margin-top: 80px;
  display: flex;
  align-items: center;
}

.movie__title {
  text-decoration: none;
  margin-right: 20px;
  color: #fafafa;
}

.movie__rateAndTime {
  display: flex;
}

.movie__rate {
  display: block;
  margin-right: 20px;
}

.movie__summary {
  margin-top: 60px;
  width: 896px;
}

.movie__genres {
  padding-left: 0px;
  list-style: none;
  display: flex;
}

.genres__item {
  margin-right: 20px;
  border: 2px solid #faebd7;
  padding: 10px;
  border-radius: 12px;
}

.movie__btn {
  float: right;
  background-color: #faebd7;
  border: none;
  padding: 10px 20px;
  border-radius: 12px;
  font-size: 16px;
}

.btn__text {
  text-decoration: none;
  color: #222;
}
